@mixin hover($_property, $_toValue) {
  transition: #{$_property} 0.3s;
  &:hover {
    #{$_property}: $_toValue;
  }
}

@mixin OnTablet {
  @media (min-width: 640px) {
    @content;
  }
}

@mixin OnDesktopSmall {
  @media (min-width: 1024px) {
    @content;
  }
}

@mixin OnDesktop {
  @media (min-width: 1600px) {
    @content;
  }
}

@mixin OnDesktopBig {
  @media (min-width: 2560px) {
    @content;
  }
}
